<template>
  <div class="registered-main">
    <c-nav-bar class="registered-main-header">
      <span slot="left" class="nav-bar-left" @click="clickLeft">
        <i class="iconfont icon-fanhui1"></i>
      </span>
    </c-nav-bar>
    <div class="registered-content">
      <div class="registered-title">
        <span>
          注册
        </span>
      </div>
      <ul class="turn-registered-content">
        <li>
          <input type="text" v-model="registeredData.mobile" placeholder="输入手机号">
        </li>
        <li class="get-verCode">
          <input :disabled="verDisabled" type="text" v-model="registeredData.authCode" placeholder="输入手机验证码">
          <c-countdown :isMobileExists="isMobileExists" :getVerUrl="getVerUrl" :phoneCode="registeredData.mobile" :totalTime="totalTime" @verCodeFlag="verCodeFlag"></c-countdown>
        </li>
        <li>
          <input type="text" v-model="registeredData.setPassWord" placeholder="设置密码">
        </li>
        <li>
          <input type="text" v-model="registeredData.password" @blur="isSamePassword" placeholder="确定密码">
        </li>
        <li>
          <input type="text" v-model="registeredData.recommenderMobile" placeholder="输入推荐人手机号(可不填)">
        </li>
      </ul>
      <div class="registered-commit">
        <van-button round type="default" :disabled="registerBtnFlag" :class="[registerBtnFlag? 'bg-gray':'bg-orange-dark']" class="commit-registered" @click="registered()">
          立即注册
        </van-button>
        <p class="agree-registered">
          注册即表示同意
          <i>
            《用户使用协议》
          </i>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import { checkMobile,checkPass } from '@/js/checkUtil'
export default {
  name: 'registered',
  data() {
    return {
      isMobileExists: true,
      registerBtnFlag: true,
      verDisabled: true,
      totalTime: 60,
      getVerUrl: '/api/authCode/sendForRegister',
      registeredData: {
        mobile: '',
        authCode: '',//验证码
        password: '',
        setPassWord: '',
        recommenderMobile: '',//推荐人手机号
      }
    }
  },
  watch: {
    registeredData: {
      handler(){
        let flag;
        for ( let key in this.registeredData ) {
          if (!this.registeredData[key] && key !== 'recommenderMobile'){
            flag=true;
            break
          }
        }
        this.registerBtnFlag=flag||false;
      },
      deep: true
    }
  },
  methods: {
    clickLeft() {
      this.$router.push({name: '登录'})
    },
    //获取验证码
    verCodeFlag(childValue) {
      this.verDisabled=childValue
    },
    //设置密码与确定密码是否一致
    isSamePassword() {
      if (this.registeredData.setPassWord!==this.registeredData.password) {
        this.$toast('两次密码不一致，请重新输入！');
        this.registeredData.password='';
      }
    },
    //确定注册
    registered() {
      //手机号校验
      if (!checkMobile(this.registeredData.mobile,'手机号')) return;
      if (!checkPass(this.registeredData.setPassWord,'密码')) return;
      if (this.registeredData.recommenderMobile && !checkMobile(this.registeredData.recommenderMobile,'推荐人手机号')) return;
      this.$http.post('/api/user/register',this.registeredData).then(response => {
        if (response.data.success) {
          console.log(response)
        } else {
          this.$toast(response.data.errorMessage)
        }
      },response => {
        console.log(response)
      })

    }
  }
}
</script>
<style scoped lang="scss">
.bg-orange-dark{
  background:linear-gradient(left bottom,$orange-dark,$orange)
}
.bg-gray{
  background: $gray;
}
.registered-main{
   .registered-main-header{
       background: #fff;
   }
   .registered-content{
       width:100%;
       top:44px;
       position:absolute;
       bottom:0;
       background: $white;
       overflow: hidden;
       padding:8%;
       display: flex;
       flex-direction: column;
        .registered-title{
            height: 100px;
            line-height: 100px;
            font-size: 25px;
            color: $orange-dark;
            span{
                font-size: 25px;
                font-weight: bold;
                color: #000;
                margin-left: 2%;
            }
        }
        .registered-commit{
            width: 100%;
            margin-top: 40px;
            .commit-registered{
                width: 100%;
                color: $white;
            }
            .agree-registered{
                width: 100%;
                line-height: 40px;
                text-align: center;
                font-size: 15px;
                i{
                    color: $orange-dark;
                }
            }
        }
        .turn-registered-content{
            li{
                width: 100%;
                height: 50px;
                line-height: 49px;
                border-bottom: 1px solid $gray;
                input{
                    width: 78%;
                    height: 20px;
                }
            }
            .get-verCode{
                display: flex;
                align-items: center;
                input{
                    flex: 1;
                }
            }
        }
    }
}
</style>
